
import React, { useEffect, useState } from "react";
import {fetchPath} from "./api";
import {TableBody, TableHead} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import _ from 'lodash';
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {getLevelDefinition} from "./utils";

const DECAY_CONST = 0.9;
export default function Leaderboards() {
  const [playerData, setPlayerData] = useState(null);

  useEffect(()=> {
    fetchPath("/tenDayLevelScoreList.json")
        .then(d=>d.json())
        .then(data => {
          _.forEach(data, (player, i)=> {
            const scores = [];
            _.forEach(player.tenDayLevelsReached, (level, i)=> {
              if (level) {
                scores.push(level * Math.pow(DECAY_CONST, i));
              }
            })
            const totalScore = _.sum(scores);
            // append total score
            player.totalScore = totalScore;
          })
          return _(data).sortBy("totalScore").reverse().value();
        })
        .then(data => setPlayerData(data))
  },[])

  const rows = _.map (playerData, (player, index)=> {
    const scoreDays = _.map(player.tenDayLevelsReached, (dayScore) => {
      const s = getLevelDefinition(dayScore)
      return s.toString()
    })
    return <TableRow key={player.playerName}>
      <TableCell>
        {index+1}
      </TableCell>
      <TableCell>
        {player.playerName}
      </TableCell>
      <TableCell>
        {player.totalScore.toFixed(2)}
      </TableCell>
      {_.map(scoreDays, (scoreDay)=> {
        return <TableCell style={{whiteSpace: "nowrap"}}>
          {scoreDay}
        </TableCell>
      })}
    </TableRow>
  });

  return (<div>
    <header>
      Spelunky 2 Leaderboards
      <div className="subtitle">Calculated using the last 10 days. More recent days have a higher influence on total score</div>
    </header>
    <div style={{width: 1024}}>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Rank</TableCell>
            <TableCell>Player</TableCell>
            <TableCell>Total Ten Day Score</TableCell>
            <TableCell colSpan={10}>Results</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows}
        </TableBody>
      </Table>
      <footer></footer>
    </div>
  </div>);
}
